li,h2, p {
    margin: 0;
}
.main {
    width: 1280px;
    background-color: aqua;
    margin-right: auto;
    margin-left: auto;
}

.main-list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    justify-content: center;
    padding: 0;
    width: 946px;
    height: 1760px;
    margin-top: 0;
    margin-bottom: 0;
    margin-right: auto;
    margin-left: auto;
    background-color: brown;
}

.main-item {
    width: 194px;
    height: 312px;
    margin-right: 56px;
    background-color: burlywood;
}

.main-item:nth-child(4n) {
    margin-right: 0;
}

.main-poster {
    width: 180px;
    height: 227px;
    margin-top: 13px;
    border-top-left-radius: 50px;
    border-bottom-right-radius: 50px;
    background-color: black;
}

.main-name,
.main-date,
.main-location {
    display: flex;
    justify-content: center;
    margin-top: 5px;

}

.main-name {
    font-style: normal;
    font-size: 16px;
    line-height: 1,22%;
}